.container {
    font-family: sans-serif;
    padding: 20px;
  }
  
  h2 {
    margin-bottom: 15px;
  }
  
  .addButton {
    background-color: #4CAF50;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-bottom: 15px;
  }
  
  .addButton:hover {
    background-color: #45a049;
  }
  
  .dataTable {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  
  .dataTable th,
  .dataTable td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .dataTable th {
    background-color: #f2f2f2;
  }
  
  .deleteButton {
    background-color: #f44336;
    color: white;
    border: none;
    padding: 8px 12px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .deleteButton:hover {
    background-color: #d32f2f;
  }
  
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modalContent {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    width: 80%;
    max-width: 500px;
  }
  
  .modal h3 {
    margin-top: 0;
    margin-bottom: 15px;
  }
  
  .formGroup {
    margin-bottom: 15px;
  }
  
  .formGroup label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  .formGroup input[type="text"],
  .formGroup input[type="email"],
  .formGroup input[type="date"],
  .formGroup select {
    width: calc(100% - 12px);
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  .submitButton,
  .cancelButton {
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 10px;
  }
  
  .submitButton {
    background-color: #007bff;
    color: white;
  }
  
  .submitButton:hover {
    background-color: #0056b3;
  }
  
  .cancelButton {
    background-color: #6c757d;
    color: white;
  }
  
  .cancelButton:hover {
    background-color: #545b62;
  }
  
  .pagination {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .pagination button {
    padding: 8px 12px;
    margin: 0 5px;
    border: 1px solid #ddd;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .pagination button.active {
    background-color: #007bff;
    color: white;
    border-color: #007bff;
  }
  
  .pagination button:hover {
    background-color: #eee;
  }